import { Autocomplete, TextField } from '@mui/material';

const selects = [
  {
    name: 'type',
    label: '',
    type: '',
    options: [
      { label: '', value: 'income', name: 'type' },
      { label: '', value: 'expense', name: 'type' },
      { label: '', value: 'transfer', name: 'type' },
    ],
    required: true,
  },
  { name: 'countIn', label: ' - ', type: '', options: [{ label: '', value: 'countIn' }] },
  { name: 'subCountIn', label: '- - ', type: '', options: [] },
  { name: 'countOut', label: ' - ', type: '', options: [] },
  { name: 'subCountOut', label: '- - ', type: '', options: [] },
  { name: 'category', label: '', type: '', options: [] },
  { name: 'counterParty', label: '', type: '', options: [] },
  { name: 'provider', label: '', type: '', options: [] },
  { name: 'customer', label: '', type: '', options: [] },
  {
    name: 'subCategory',
    label: '-',
    type: '',
    options: [],
  },
  {
    name: 'status',
    label: '',
    type: '',
    options: [
      { label: '', value: 'approved', name: 'status' },
      { label: '', value: 'ok', name: 'status' },
      { label: '', value: 'problem', name: 'status' },
    ],
  },
];

const Select = ({ name, onSelect, disabled, data = {}, ...props }) => {
  let select = selects.find(el => el.name === name);
  const { label = null } = select;

  if (!select) {
    return null;
  }

  let settings = {
    ...select,
    // isOptionEqualToValue()
    // defaultValue: select?.options.find(el => el.value === data[name])?.label || null,
    options: select?.options || [],
  };
  // console.log('settings', settings.defaultValue, 'data', data);

  return (
    <Autocomplete
      {...settings}
      {...props}
      disablePortal
      onChange={onSelect}
      disabled={disabled}
      renderInput={params => <TextField variant="standard" {...params} {...{ label }} />}
    />
  );
};
export default Select;
